import React, { useEffect, useState } from "react";
import LineIcon from "react-lineicons";

class ImageList extends React.Component {

  list() {
    let list = this.props.files.map((n, i) => {
      return <Item key={n.id} file={n} upd={this.props.upd} index={i} {...this.props} />
    })
    if (list.length <= 0) {
      list = 'нет фото';
    }

    return (
      <div className="list mt-3" change-mod={this.props.changeMod ? 1 : 0}>
        {list}
      </div>
    )

  }

  render() {
    return this.list();
  }
}

function Item(props) {

  let n = props.file;
  let o = n.options;

  const [deg, setRotate] = useState(false);
  const [change, changed] = useState(false);

  const rotate = () => {
    setRotate(!deg);
  }

  const changeIT = () => {
    if (props.changeMod) {
      props.upd('changed', !o.changed, props.index);
    }
  }

  useEffect(() => {
    props.upd('rotate', deg, props.index);
  }, [deg, change])

  if (o === undefined) return null;

  return (
    <div onClick={changeIT} className="file d-flex" changed={o.changed ? 'changed' : ''}>
      <Image deg={deg} o={o} n={n} />
      <div className="file-options">
        <div className="o format"><LineIcon name="lni lni-ruler-alt" />&nbsp;{o.format ? o.format : 'Нет'}</div>
        <div className="o paper"><LineIcon name="lni lni-write" />&nbsp;{o.paper ? o.paper : 'Нет'}</div>
        <div className="o crop"><LineIcon name="lni lni-crop" />&nbsp;{o.cadr ? o.cadr : 'Нет'}</div>
        <div className="o count"><LineIcon name="lni lni-shopping-basket" />&nbsp;{o.count ? o.count : 'Нет'}</div>
        <div className="delimeter"></div>
        <ItemOptions
          index={props.index}
          setup={props.setup}
          remove={props.remove}
          rotate={rotate} />
      </div>
    </div>
  )
}

function Image(props) {
  const [deg, o, n] = [props.deg, props.o, props.n];
  const src = o.cropped ? o.cropped : window.URL.createObjectURL(n.file);

  return (
    <div className={`image-wrapper ${deg ? 'v' : 'h'} ${o.cadr ? 'cadr-on' : ''}`} style={{
      backgroundImage: `url(${src})`
    }}></div>
  )
}

function ItemOptions(props) {
  return (
    <div className="item-option-list">
      <ItemOptionListBtn
        actionFunction={props.setup}
        iconClass="lni lni-cog"
        itemClass="setup"
        index={props.index}
      />
      <ItemOptionListBtn
        actionFunction={props.rotate}
        iconClass="lni lni-spinner-arrow"
        itemClass="rotate"
        index={props.index}
      />
      <ItemOptionListBtn
        actionFunction={props.remove}
        iconClass="lni lni-close"
        itemClass="close"
        index={props.index}
      />
    </div>
  )
}

export function ItemOptionListBtn(props) {
  const { actionFunction, iconClass, itemClass, index } = { ...props }
  return (
    <div onClick={() => actionFunction(index)} className={`item-option ${itemClass}`}><LineIcon name={iconClass} /></div>
  )
}

export default ImageList;