import React, { useEffect, useState, useReducer } from "react";
import ImageList from "./list";
import { GroupOptions } from "./file_options_form";
import { SetupImagePopup } from "./setup-image-popup";
import LineIcon from "react-lineicons";

export function Main() {

  const [files, addFiles] = useState([]);
  const [changeMod, setChangingMod] = useState(false);
  const [setupImageMod, setSetupImageMod] = useState(null);
  const [lastModifiedTime, setLastModifiedTime] = useState(new Date().getTime());

  const addFilesFunc = (curFiles) => {
    let newFiles = [];
    if (curFiles.length > 0) {
      for (let i = 0; i < curFiles.length; i++) {
        let img = newImage(curFiles[i]);
        newFiles.push(img);
      }
      addFiles(files.concat(newFiles));
      setChangingMod(true);
    }
  }

  const removeFile = (fileIndex) => {
    let donor = files;
    donor.splice(fileIndex, 1);
    addFiles(donor);
    setLastModifiedTime(new Date().getTime());
  }

  const useChanging = () => {
    if (changeMod) {
      let donor = files;
      donor.forEach((f) => {
        f.options.changed = false;
      })
      addFiles(donor);
    }
    setChangingMod(!changeMod);
  }

  const updateFile = (option, newFileData, index) => {
    console.log(option, newFileData, index);
    if (files !== undefined) {
      let f = files;
      f[index].options[option] = newFileData;
      addFiles(f);
      setLastModifiedTime(new Date().getTime());
    }
  }

  const newImage = (file) => {
    return {
      id: ID(file),
      file: file,
      body: '',
      options: {
        count: 1,
        format: '10x15',
        paper: 'матовая',
        cadr: 0,
        rotate: false,
        changed: true,
        cropped: null,
      }
    };
  }

  const ID = (file) => {
    return file.size + '_' + file.name;
  }

  const massSetOptions = (formData) => {
    let donor = files;

    donor.forEach((f) => {
      if (f.options.changed) {
        formData.forEach((el) => {
          f.options[el.name] = el.value;
        })
        // Передавалась ли опция кадрирования
        const isCadr = formData.findIndex((el) => { return (el.name === 'cadr') })
        // Если не передавалась, снимаем опции у выбранных фото
        if (isCadr < 0) {
          f.options.cadr = false;
        }
      }
    })

    addFiles(donor);
    setLastModifiedTime(new Date().getTime());
  }

  const setupImage = (index) => {
    setSetupImageMod(index);
  }

  const closeModal = () => {
    setSetupImageMod(null);
  }

  return (
    <>
      <Group
        addFiles={addFilesFunc}
        setChangingMod={useChanging}
        changeMod={changeMod}
        upd={updateFile}
        remove={removeFile}
        massSetOptions={massSetOptions}
        setup={setupImage}
        files={files} />

      {typeof setupImageMod === 'number' &&
        <SetupImagePopup
          fileIndex={setupImageMod}
          file={files[setupImageMod]}
          closeModal={closeModal}
          upd={updateFile}
        />}
    </>
  )

}

function Group(props) {

  const addFiles = (e) => {
    const files = e.target.files;
    props.addFiles(files);
  }

  return (
    <div className="row">
      <div className="col-12 col-lg-9">
        <div className="card border-secondary my-3">
          <div className="card-header d-flex justify-content-between align-items-center">
            Ваши фотографии: {props.files.length}
            <MassSetOptionsBtn modOn={props.changeMod} setChangingMod={props.setChangingMod} />
          </div>
          <div className="card-body">
            <div className="image-list">
              <label>
                <input onChange={addFiles} className="form-control" type="file" multiple />
              </label>
              <ImageList {...props} />
            </div>
          </div>
          <div className="card-footer d-flex justify-content-end">
            <div className="btn btn-success disabled">Перейти к оформлению заказа</div>
          </div>
        </div>
      </div>
      <div className="col-12 col-lg-3">
        <GroupOptions mod={true} massSetOptions={props.massSetOptions} {...props} />
      </div>
    </div>
  )
}

function MassSetOptionsBtn(props) {
  let btnText = 'Массовое выделение';
  if (props.modOn) {
    btnText = 'снять выделение';
  }
  return (
    <div onClick={props.setChangingMod} className="btn btn-info text-white">{btnText}</div>
  )
}